.App {
  text-align: center;
  padding: 1%;
  background-color: #0575e6;
}
.cont {
  background-color: white;
  width: 100%;
  border-radius: 10px;
  padding: 1%;
}
.show-PM {
  width: 100%;
  height: 320px;
  margin-top: 3%;
}
.show-PM img:nth-of-type(2) {
  margin: 15% 2px 0 0;
  width: 60%;
}
.show-PM img:nth-of-type(1) {
  width: 25%;
}
.show-data {
  width: 100%;
  height: 320px;
  border-radius: 5px;
  background-color: white;
  position: relative;
  margin-top: 3%;
  box-shadow: 8px 5px 5px rgb(189, 187, 187);
}
.show-data-item {
  width: 100%;
  height: 80%;
  padding: 8% 1%;
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  border-radius: 5px 5px 0 0;
}
.bg1 {
  background: #11998e; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #38ef7d,
    #11998e
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #38ef7d,
    #11998e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.bg2 {
  background: #2193b0; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #6dd5ed,
    #2193b0
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #6dd5ed,
    #2193b0
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.bg3 {
  background: #ffe000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #799f0c,
    #ffe000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #799f0c,
    #ffe000
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.bg4 {
  background: #00f260; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #0575e6,
