.show-data-item h1 {
  font-weight: 500;
  font-size: 3.7rem;
}
.show-data-item p {
  font-weight: 800;
  font-size: 3.5rem;
}
.chart {
  position: absolute;
  background-color: rgb(255, 255, 255);
  width: 100%;
  bottom: 0;
  height: 20%;
  border-radius: 5px 5px 0 0;
}
.chart:hover {
  height: 85%;
  border-radius: 5px 5px 0 0;

}
